header{position:absolute;z-index:1;display:flex;flex-direction:column;justify-content:space-between;top:0;left:0;width:100%;height:100vh;min-height:54rem;padding:2rem var(--margin)}@media screen and (max-width: 768px){header{height:90vh}}@media screen and (max-width: 544px){header{min-height:40rem}}header nav{display:flex;flex-direction:column;align-items:center;gap:.25rem;width:100%}header nav:nth-of-type(2) div{justify-content:flex-end;height:1.5em}header nav:nth-of-type(2) div div{display:flex;flex-direction:column}header nav div{display:flex;overflow:hidden}header nav .text,header nav span{padding:0 .25rem}header nav span{line-height:1.5}