.calendar-container{display:flex;flex-direction:column;width:100vw}.calendar-container .calendar{position:absolute;z-index:1;display:flex;flex-direction:column;align-items:center;justify-content:center;left:50%;width:50%;height:100%;padding:calc(var(--margin-row)*.5) var(--margin);padding-left:.25rem}@media screen and (max-width: 1024px){.calendar-container .calendar{position:relative}}@media screen and (max-width: 768px){.calendar-container .calendar{left:0;width:100%;padding-right:var(--margin);padding-left:var(--margin)}}.calendar-container .calendar .calendar-content{display:grid;grid-template-columns:repeat(7, 1fr);align-items:center;width:100%;max-width:32rem;background:var(--white-color)}.calendar-container .calendar .calendar-content .text{grid-column:1/-1;padding:2rem 1rem 0;text-align:center}.calendar-container .calendar .calendar-content div{grid-column:1/-1;display:grid;grid-template-columns:repeat(7, 1fr)}.calendar-container .calendar .calendar-content div.day{padding:0 1rem 1rem;margin-top:1.5rem}.calendar-container .calendar .calendar-content div.day::after{content:"";position:absolute;display:block;left:1rem;bottom:0;width:calc(100% - 2rem);height:1px;background:var(--foreground-color);opacity:.375}.calendar-container .calendar .calendar-content div.day span{font-weight:var(--label-weight)}.calendar-container .calendar .calendar-content div.day span:nth-child(1),.calendar-container .calendar .calendar-content div.day span:nth-child(7){color:var(--brown-color)}.calendar-container .calendar .calendar-content div.date{padding:0 1rem 2rem}.calendar-container .calendar .calendar-content div.date span{margin-top:2rem}@media screen and (max-width: 544px){.calendar-container .calendar .calendar-content div.date span{margin-top:1.5rem}}.calendar-container .calendar .calendar-content div.date span:nth-child(7n),.calendar-container .calendar .calendar-content div.date span:nth-child(7n+1){color:var(--brown-color)}.calendar-container .calendar .calendar-content div.date span.prev,.calendar-container .calendar .calendar-content div.date span.next{color:var(--foreground-color);opacity:.375}.calendar-container .calendar .calendar-content div.date span.pres.-s11{z-index:1;color:var(--white-color)}.calendar-container .calendar .calendar-content div.date span.pres.-s11::before{content:"";position:absolute;z-index:-1;display:block;top:50%;left:50%;width:2em;height:2em;background:var(--brown-color);border-radius:50%;transform:translate3d(-50%, -50%, 0)}.calendar-container .calendar .calendar-content span{display:block;font-size:var(--label-size);letter-spacing:var(--font-spacing);line-height:var(--font-height);text-align:center}@media screen and (max-width: 768px){.calendar-container .calendar .calendar-content span{font-size:var(--font-size)}}.calendar-container .timer-container{position:absolute;z-index:1;display:flex;flex-direction:column;align-self:center;gap:.5rem;bottom:var(--margin);left:var(--margin);color:var(--white-color)}@media screen and (max-width: 768px){.calendar-container .timer-container{position:relative;bottom:auto;left:auto;padding:0 var(--margin) calc(var(--margin-row)*.5)}}@media screen and (max-width: 768px){.calendar-container .timer-container .text{text-align:center}}.calendar-container .timer-container .timer{display:flex;gap:.5rem}.calendar-container .timer-container .timer div{display:flex;flex-direction:column;gap:.25rem}@media screen and (max-width: 768px){.calendar-container .timer-container .timer div{align-items:center}}.calendar-container .timer-container .timer div .unit{font-size:var(--subtext-size);opacity:.75}.calendar-container .timer-container .timer span{font-size:var(--headline-size);font-weight:var(--title-weight);letter-spacing:var(--headline-spacing);line-height:1;color:var(--white-color)}@media screen and (max-width: 768px){.calendar-container .timer-container .timer span{font-size:var(--subheadline-size);letter-spacing:var(--subheadline-spacing)}}.calendar-container .img{width:100%;padding-top:56.25%}@media screen and (max-width: 1024px){.calendar-container .img{position:absolute;height:100%}}.calendar-container .img img{position:absolute;top:50%;left:50%;width:100%;height:100%;transform:translate3d(-50%, -50%, 0);filter:brightness(75%)}