button,.button{display:flex;align-items:center;justify-content:space-between;gap:.5rem;width:100%;padding:.5rem .75rem;overflow:hidden}button span,.button span{font-size:var(--font-size);font-weight:var(--label-weight);letter-spacing:var(--font-spacing);line-height:var(--font-height);text-wrap:nowrap}button{background:rgba(var(--foreground-rgb), 0.625)}button:hover{background:rgba(var(--foreground-rgb), 1)}button span{color:var(--background-color)}button svg{fill:var(--background-color)}.button{border:1px solid var(--border-color)}@media screen and (max-width: 768px){.button{max-width:unset}}.button:hover{border-color:rgba(var(--foreground-rgb), 1)}