section#hero{justify-content:center;height:100vh;min-height:54rem}@media screen and (max-width: 768px){section#hero{height:90vh}}@media screen and (max-width: 544px){section#hero{min-height:40rem}}section#hero .hero{z-index:1;width:100vw;mix-blend-mode:difference}section#hero .hero h1{font-size:12vw;text-wrap:nowrap;word-break:unset;color:var(--white-color)}@media screen and (max-width: 1024px){section#hero .hero h1{font-size:18vw}}section#hero .card{position:absolute;top:50%;left:50%;width:44rem;height:44rem;background:var(--white-color)}@media screen and (max-width: 1024px){section#hero .card{width:75%;height:auto;padding-top:75%}}@media screen and (max-width: 768px){section#hero .card{padding-top:83.125%}}@media screen and (max-width: 544px){section#hero .card{width:87.5%;padding-top:116.375%}}section#hero .card .card-content{position:absolute;display:flex;flex-direction:column;top:0;left:0;width:100%;height:100%;padding:.5rem}section#hero .card .card-content .img{display:flex;align-items:center;justify-content:center;flex-shrink:1;height:100%}section#hero .card .card-content .img img{width:62.5%;height:83.125%}@media screen and (max-width: 544px){section#hero .card .card-content .img img{width:72.5%;height:72.5%}}